@import "https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,100..900;1,100..900&display=swap";
* {
  box-sizing: border-box;
  background-color: #1d1d1d;
  margin: 0;
  padding: 0;
  font-family: Libre Franklin, sans-serif;
}

.game {
  max-width: 24rem;
  margin: 5rem auto;
  position: relative;
}

.header {
  text-align: center;
  color: #fff;
  font-size: 1.25rem;
  font-weight: 600;
}

.retry-btn {
  color: #fff;
  border: none;
  font-size: 1.8rem;
  font-weight: bold;
  position: absolute;
  top: -2.5rem;
  left: 1rem;
}

.retry-btn:hover {
  color: #cbcbcb;
}

.guesses-container {
  grid-template-columns: 1fr, 1fr, 1fr, 1fr, 1fr 1fr;
  gap: 5px;
  width: 14rem;
  margin: 2rem auto 8rem;
  display: grid;
}

.row-guesses {
  justify-content: space-between;
  display: flex;
}

.letter {
  color: #fff;
  text-align: center;
  border: 1px solid #505050;
  width: 40px;
  height: 40px;
  font-weight: bold;
  line-height: 40px;
}

.keyboard {
  gap: 6px;
  display: grid;
}

.keyboard-row-1, .keyboard-row-2, .keyboard-row-3 {
  justify-content: center;
  gap: 4px;
  display: flex;
}

.keyboard button {
  color: #fff;
  background-color: gray;
  border: none;
  border-radius: 4px;
  width: 32px;
  height: 40px;
  padding: 6px;
  font-size: .9rem;
  font-weight: bold;
}

.keyboard button:hover {
  cursor: pointer;
  background-color: #a0a0a0;
}

.keyboard .enter-btn, .keyboard .backspace-btn {
  width: 40px;
  font-size: 8px;
}

.keyboard .backspace-btn {
  font-size: 12px;
}

.wrong-letter {
  background-color: #181818;
}

.partial-correct {
  background-color: #ffbf00;
}

.correct-letter {
  background-color: #0ac400;
}
